import './LoginForm.css';
import React, { ChangeEvent, FormEvent, useState } from 'react';
import axios from 'axios';
import { InputUser } from '../InputUser';
import PersonIcon from '@mui/icons-material/Person';
import LockIcon from '@mui/icons-material/Lock';


type Props = {
    user: InputUser,
    setUser: Function,
    setLogin: Function,
    navigate: any
}

const LoginForm: React.FC<Props> = ({user, setUser, setLogin, navigate}) => {
    const [error, setError] = useState<any>();

    function handleChange (event: ChangeEvent<HTMLInputElement>) {
        console.log(event.target.name);
        console.log(event.target.value);
        setUser ((prevState: InputUser) => {
            return {...prevState, [event.target.name]: event.target.value}
        });
    }

    function handleSubmit (event: FormEvent<HTMLFormElement>) {
        event.preventDefault();
        (async () => {
            try {
                if (user.username !== undefined && user.password !== undefined) {
                    const {data} = await axios.get(`${process.env.REACT_APP_SERVER_URL}/test`, {
                        headers: {
                            Authorization: 'Bearer ' + btoa(JSON.stringify(user))
                        },
                    });
                    setLogin(data);
                    navigate('/uploader');
                    console.log("response: " + data);
                    console.log("response-dateityp: " + typeof(data));
                }else {
                    throw new Error("Ungültige Angaben");
                }
            } catch (error) {
                setError(error);
            }
        })();
    }
    
    return (
        <>
        <div>{error}</div>
        <div className='wrapper'>
            <div className='box'>
                <div className='text'>
                    <p>Login</p>
                </div>
           <form onSubmit={handleSubmit}>
              <div className='input-box'>
                  <input
                      type='text'
                      id='username'
                      name='username'
                      placeholder='Username'
                      value={user.username}
                      onChange={handleChange}
                  />
                  <label><PersonIcon/></label>
              </div>
              <div className='input-box'>
                  <input
                      type='password'
                      id='password'
                      name='password'
                      placeholder='Password'
                      value={user.password}
                      onChange={handleChange}
                  />
                  <label><LockIcon/></label>
              </div>
              <div>
                  <button type='submit' className='btn'>Login</button>
              </div>
           </form>
           </div>
        </div>
        </>
   );
}

export default LoginForm;