import './SingleData.css';
import React from 'react';
import TableCell from './TableCell';

type Props = {
    item: any
}

const SingleData: React.FC<Props> = ({item}) => {
   return (
      <tr>
        {
            item.map((e: any) => {
              return <TableCell item={e} />;
            })
        }
      </tr>
   );
}

export default SingleData;