import { InputUser } from '../InputUser';
import './UserData.css';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import SingleData from './SingleData';

type Props = {
   user: InputUser
}

const UserData: React.FC<Props> = ({user}) => {
    const [data, setData] = useState<Array<String>>(["0|Christian|christiandersack@gmail.com|ichbincool|inbearbeitung", "1|Daniel|danieldersack@gmail.com|ichbinauchcool|abgeschlossen"]);
    const [error, setError] = useState<any>();
    

    useEffect (() => {
        (async () => {
            try {
                if (user.username !== undefined && user.password !== undefined) {
                    const {data} = await axios.get(`${process.env.REACT_APP_SERVER_URL}/printdata`, {
                        headers: {
                            Authorization: 'Bearer ' + btoa(JSON.stringify(user))
                        },
                    });
                    setData(data);
                    console.log("response: " + data);
                    console.log("response-dateityp: " + typeof(data));
                }else {
                    throw new Error("Ungültige Angaben");
                }
            } catch (error) {
                setError(error);
            }
        })();
    }, []);

   return (
      <div>
        <h1>{error}</h1>
      <table>
          <thead>
          <tr>
              <th>Kundennummer</th>
              <th>Name</th>
              <th>Email</th>
              <th>Nachricht</th>
              <th>Status</th>
          </tr>
          </thead>
          <tbody>
              {
                  data.map((e) => {
                    const elements = e.split('|');
                    return <SingleData key={elements[0]} item={elements} />;
                  })
              }
          </tbody>
      </table>
      </div>
   );
}

export default UserData;