import React, { useState } from 'react';
import './App.css';
import LoginForm from './LoginHandler/LoginForm.component';
import { Container } from '@mui/material';
import {Routes, Route, Navigate, useNavigate} from 'react-router-dom';
import { InputUser } from './InputUser';
import UserData from './UserData/UserData';

const initialUser: InputUser = {
  username: '',
  password: ''
}

function App() {
  const navigate = useNavigate();
  const [user, setUser] = useState<InputUser>(initialUser);
  const [login, setLogin] = useState<boolean>(false);
  

  return (
    <>
      <Container>
        <Routes>
          <Route 
            path='/data'
            element={(login === true) ? <UserData user={user} /> : <Navigate to="/login" />}
          />
          <Route 
            path='/login'
            element={<LoginForm user={user} setUser={setUser} setLogin={setLogin} navigate={navigate} />}
          />
          <Route 
            path='/'
            element={<Navigate to="/data" />}
          />
          <Route 
            path='*'
            element={<Navigate to="/data" />}
          />
        </Routes>
      </Container>
    </>
  );
}

export default App;
